/* 全局變數 */
$primary-color: #8b5a2b;
$hover-color: #a07246;
$button-padding: 12px 24px;
$button-radius: 10px;
$button-transition: background-color 0.3s ease;
$box-shadow: 0 3px 6px rgba(0, 0, 0, 0.3);
$text-shadow: 0.2vw 0.2vw 0.4vw rgba(0, 0, 0, 0.7);

/* 主容器樣式 */
.container5 {
    position: relative;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Comic Sans MS', cursive, sans-serif;

    &.hide-background {
        background-image: none;
    }
}

/* 按鈕容器 */
.buttons-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 20px;
    width: 80%;
    max-width: 600px;
    margin: 0 auto;
    transform: translate(-50%, -50%);
}

/* 按鈕樣式 */
.foundButton5,
.endGameButton5 {
    padding: $button-padding;
    font-size: 18px;
    background-color: $primary-color;
    color: #fff;
    border: none;
    border-radius: $button-radius;
    cursor: pointer;
    transition: $button-transition;
    outline: none;
    box-shadow: $box-shadow;
    text-align: center;
    line-height: 1.5;
    flex: 1;
    margin: 0 10px;

    &:hover {
        background-color: $hover-color;
    }
}

/* 隱藏背景樣式 */
body.hide-background {
    background: none !important;
}

/* 信息文本樣式 */
.info-text {
    font-size: 18px;
    color: #333;
    margin-bottom: 10px;
    background-color: rgba(204, 204, 204, 0.8);
    padding: 10px;
    border-radius: 5px;
    text-align: center;
    line-height: 1.6;
    width: 100%;
    max-width: 600px;
    margin: 0 auto;
}

/* 媒體查詢：適應平板設備 */
@media (max-width: 1024px) {

    .foundButton5,
    .endGameButton5 {
        padding: 10px 20px;
        font-size: 16px;
    }

    .info-text {
        font-size: 16px;
        padding: 8px;
    }
}

/* 媒體查詢：適應手機設備 */
@media (max-width: 768px) {

    .foundButton5,
    .endGameButton5 {
        padding: 8px 16px;
        font-size: 14px;
    }

    .info-text {
        font-size: 14px;
        padding: 6px;
    }
}

/* 媒體查詢：適應小手機設備 */
@media (max-width: 480px) {
    .buttons-container {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 10px;
    }

    .foundButton5,
    .endGameButton5 {
        padding: 6px 12px;
        font-size: 12px;
        margin: 0;
        width: 80%;
        max-width: 300px;
    }

    .info-text {
        font-size: 12px;
        padding: 4px;
        width: 80%;
        max-width: 300px;
    }
}